import "../styles/editor.scss";
import { __ } from "@wordpress/i18n";
import { withInstanceId, compose } from "@wordpress/compose";
import { useEffect, useState, useRef } from "@wordpress/element";
import { withSelect } from "@wordpress/data";
import { getSettings, dateI18n } from "@wordpress/date";
import { decodeEntities } from "@wordpress/html-entities";

import {
	RichText,
	BlockControls,
	useBlockProps,
	AlignmentToolbar,
} from "@wordpress/block-editor";

import { Placeholder, Spinner } from "@wordpress/components";

import InspectorPanel from "./inspector";
import { prefix } from "../../../constants";
import {
	isUndefined,
	pickBy,
	getTypoStyle,
	findIndex,
	getDimensionStyle,
	capitalize,
} from "../../../helpers";

import Slider from "react-slick";

const Edit = (props) => {
	const {
		attributes: {
			alignment,
			padding,
			enableCategory,
			categories,
			metaTypo,
			headingTypo,
			bgColor,
			color = "#ffffff",
			title,
		},
		setAttributes,
		latestPosts,
		clientId,
	} = props;

	if (!Array.isArray(latestPosts) || latestPosts.length === 0) {
		return (
			<>
				<Placeholder icon="admin-post" label={__("News 1", "rise-blocks")}>
					{!Array.isArray(latestPosts) ? (
						<Spinner />
					) : (
						__("No posts found.", "rise-blocks")
					)}
				</Placeholder>
			</>
		);
	}

	const [hover, setHover] = useState({
		next: false,
		prev: false,
	});

	const sliderRef = useRef(null);

	useEffect(() => {
		const { setAttributes, instanceId } = props;

		setAttributes({
			block_id: `${prefix}-news-2-block-instance-${instanceId}-${clientId}`,
		});
	}, []); // Empty dependency array for componentDidMount

	const getTitle = ({ title }, i) => {
		const {
			attributes: { enableTitle, titleTypo, color },
		} = props;

		if (enableTitle) {
			const style = {
				...getTypoStyle(titleTypo),
				color: color,
			};

			return (
				<h2 className={`${prefix}-news-2-post-title`}>
					<a key={i} onClick={(e) => e.preventDefault()} href="#" style={style}>
						{decodeEntities(title.rendered.trim()) ||
							__("(Untitled)", "rise-blocks")}
					</a>
				</h2>
			);
		}
	};

	const getImgUrl = ({ _embedded }) => {
		const {
			attributes: { imageSize },
		} = props;

		let img = "";

		if (
			!isUndefined(_embedded) &&
			!isUndefined(_embedded["wp:featuredmedia"]) &&
			!isUndefined(_embedded["wp:featuredmedia"][0].source_url) 

		) {
			var size = _embedded["wp:featuredmedia"][0].media_details.sizes;
			if (size[imageSize]) {
				img = size[imageSize] ? size[imageSize].source_url : "";
			} else {
				img =  _embedded["wp:featuredmedia"][0].source_url ;
			}
		}

		return img;
	};

	const meta = (post) => {
		const { _embedded, date_gmt } = post;
		const {
			attributes: { color, enableDate, enableAuthor, metaTypo },
		} = props;

		const style = {
			...getTypoStyle(metaTypo),
			color: color,
		};

		const dateFormat = getSettings().formats.date;

		if (enableAuthor || enableDate) {
			return (
				<div className={`${prefix}-news-2-meta-wrapper`}>
					{enableAuthor && _embedded && _embedded.author[0].avatar_urls && (
						<div className={`${prefix}-news-2-author meta-content`}>
							<a
								onClick={(e) => e.preventDefault()}
								style={style}
								href="#"
								title={capitalize(_embedded.author[0].name)}
							>
								<i className="fa fa-user"></i> {_embedded.author[0].name}
							</a>
						</div>
					)}
					{enableDate && (
						<div className={`${prefix}-news-2-post-date meta-content`}>
							<a onClick={(e) => e.preventDefault()} style={style} href="#">
								<span>
									<i className="fa fa-calendar"></i>{" "}
									{dateI18n(dateFormat, date_gmt)}
								</span>
							</a>
						</div>
					)}
				</div>
			);
		}
	};

	const getSliderSettings = (length) => {
		const { arrowColor, arrowBgColor, slidesToShow = 3 } = props.attributes;

		const onEnter = (type) => {
			return (e) => setHover((prev) => ({ ...prev, [type]: true }));
		};

		const onLeave = (type) => {
			return (e) => setHover((prev) => ({ ...prev, [type]: false }));
		};

		const buttonStyle = {
			backgroundColor: arrowBgColor,
			color: arrowColor,
		};

		const Next = () => {
			return (
				<button
					onMouseEnter={onEnter("next")}
					onMouseLeave={onLeave("next")}
					onClick={() => sliderRef.current.slickNext()}
					type="button"
					className={`${prefix}-next-arrow ${prefix}-slider-arrow`}
					style={buttonStyle}
				>
					<i className="fa fa-angle-right"></i>
				</button>
			);
		};

		const Prev = () => {
			return (
				<button
					onMouseEnter={onEnter("prev")}
					onMouseLeave={onLeave("prev")}
					onClick={() => sliderRef.current.slickPrev()}
					style={buttonStyle}
					type="button"
					className={`${prefix}-prev-arrow ${prefix}-slider-arrow`}
				>
					<i className="fa fa-angle-left"></i>
				</button>
			);
		};

		const settings = {
			slidesToShow: length > slidesToShow ? slidesToShow : length,
			slidesToScroll: 1,
			autoplay: false,
			infinite: true,
			arrows: true,
			dots: false,
			nextArrow: <Next />,
			prevArrow: <Prev />,
			responsive: [
				{
					breakpoint: 767,
					settings: {
						slidesToShow: 1,
					},
				},
			],
		};

		return settings;
	};

	const settings = getSliderSettings(latestPosts.length);
	const blockProps = useBlockProps({
		style: {
			backgroundColor: bgColor,
			...getDimensionStyle("padding", padding),
		},
		className: `${prefix}-news-2-wrapper`,
	});

	return (
		<>
			<BlockControls>
				<AlignmentToolbar
					value={alignment}
					onChange={(alignment) => setAttributes({ alignment })}
				/>
			</BlockControls>
			<section {...blockProps}>
				<RichText
					placeholder={__("Title", "rise-blocks")}
					tag="h2"
					style={{ color, textAlign: "left", ...getTypoStyle(headingTypo) }}
					className={`${prefix}-news-2-title`}
					value={title}
					onChange={(title) => setAttributes({ title })}
				/>

				<Slider
					className={`${prefix}-align-${alignment}`}
					ref={sliderRef}
					{...settings}
				>
					{latestPosts.map((post, i) => {
						const style = {
							backgroundImage: "url(" + getImgUrl(post) + ")",
						};

						const rb_cat = post["rise-blocks_categories"];
						let cat = "";

						if (
							isUndefined(categories) &&
							Array.isArray(rb_cat) &&
							rb_cat.length
						) {
							cat = rb_cat[0].cat_name;
						} else {
							const index = findIndex(rb_cat, {
								term_id: parseInt(categories),
							});
							cat = rb_cat[index].cat_name;
						}

						const catStyle = {
							...getTypoStyle(metaTypo),
							color: color,
						};

						return (
							<div key={i} className={`${prefix}-news-2-card-wrapper`}>
								<div>
									<div
										style={style}
										className={`${prefix}-news-2-card-image`}
									></div>
								</div>

								<div className={`${prefix}-news-2-card`}>
									<div className={`${prefix}-news-2-body`}>
										<div className={`${prefix}-news-2-body-inner`}>
											{enableCategory && (
												<div
													className={`${prefix}-news-2-post-cat meta-content`}
												>
													<a
														onClick={(e) => e.preventDefault()}
														style={catStyle}
														href="#"
													>
														<span>{cat}</span>
														<span
															className="line"
															style={{ backgroundColor: color }}
														></span>
													</a>
												</div>
											)}

											{getTitle(post, i)}

											{meta(post)}
										</div>
									</div>
								</div>
							</div>
						);
					})}
				</Slider>
			</section>
			<InspectorPanel {...props} />
		</>
	);
};

const applyWithSelect = withSelect((select, { attributes }) => {
	const { postsToShow, order, orderBy, categories } = attributes;
	const { getEntityRecords } = select("core");

	const latestPostsQuery = pickBy(
		{
			categories,
			order,
			orderby: orderBy,
			per_page: postsToShow,
			_embed: true,
		},
		(value) => !isUndefined(value),
	);

	return {
		latestPosts: getEntityRecords("postType", "post", latestPostsQuery),
	};
});

export default compose(applyWithSelect, withInstanceId)(Edit);
