import { responsiveViewsIcon, prefix } from '../constants';
import { ButtonGroup, Button } from '@wordpress/components';

const ResponsiveViewsIcons = ({
  onClick = e => {},
  responsiveViews,
  activeView = 'desktop',
  className = '',
}) => {
  const responsiveViewLists = responsiveViews.map((view, i) => {
    const icon = responsiveViewsIcon[view] ? responsiveViewsIcon[view] : '';
    return (
      <Button
        key={i}
        onClick={e => onClick(view)}
        className={`${activeView === view ? 'active' : ''}`}
      >
        <span className={icon}></span>
      </Button>
    );
  });
  return (
    <ButtonGroup className={`${className} ${prefix}-responsive-view-icons`}>
      {responsiveViewLists}
    </ButtonGroup>
  );
};

export default ResponsiveViewsIcons;
