import { useState } from "@wordpress/element";
import { Button } from "@wordpress/components";
import { __ } from "@wordpress/i18n";

/* Custom components */
import ResponsiveViewsIcons from "../components/responsive-view-icons";
import SizeUnit from "../components/size-unit";
import InputGroup from "../components/input-group";
import { merge, globalize } from "../helpers";

/* Others */
import { prefix, defaultDimensionValue } from "../constants";

const DimensionControl = (props) => {
	const { label } = props;
	const getData = () => {
		const { value = {} } = props;
		const { values, ...defaultDimensionValueRest } = defaultDimensionValue;
		const data = { ...defaultDimensionValueRest, ...value };
		const { properties, responsiveViews } = data;

		return {
			...data,
			values: value.values
				? getViewData(properties, data.values, responsiveViews)
				: getViewData(properties, values, responsiveViews),
		};
	};

	const getViewData = (properties, responsiveValues, responsiveViews) => {
		let viewData = {};

		responsiveViews.forEach((v, i) => {
			let value = responsiveValues[v];
			let currentValue = properties.map((p, i) =>
				value && value.length - 1 >= i ? value[i] : 0,
			);
			viewData = { ...viewData, [v]: currentValue };
		});

		return viewData;
	};

	const handleChange = (name, data) => {
		const oldData = getData();
		const { isLinkActive, values } = oldData;
		const { onChange = () => {} } = props;

		let newData = {};

		if ("updateValues" === name) {
			let { index, value } = data;
			newData = merge({}, oldData, {
				values: {
					[activeView]: isLinkActive
						? values[activeView].map(() => parseInt(value))
						: values[activeView].map((v, i) =>
								index === i ? parseInt(value) : v,
						  ),
				},
			});
		} else if ("updateLink" === name) {
			newData = merge({}, oldData, {
				isLinkActive: !isLinkActive,
			});
		} else if ("updateUnit" === name) {
			newData = merge({}, oldData, {
				activeUnit: data,
			});
		}

		onChange(newData);
	};

	const reset = () => {
		const { defaultValue, onChange } = props;
		defaultValue && onChange(defaultValue);
	};

	const {
		responsiveViews,
		isLinkActive,
		activeUnit,
		units,
		values,
		properties,
	} = getData();

	const [activeView, setActiveView] = useState(responsiveViews[0]);

	return (
		<div className={`${prefix}-dimension-input`}>
			<div
				className={`${prefix}-label-section`}
				style={{
					display: "flex",
				}}
			>
				{label && (
					<label
						// The inline styling is used here instead of the CSS file because
						// This styling is only required in this specific case.
						style={{
							maxWidth: "45%",
						}}
						className="components-base-control__label"
					>
						{label}
					</label>
				)}

				<ResponsiveViewsIcons
					responsiveViews={responsiveViews}
					activeView={activeView}
					onClick={(view) => setActiveView(view)}
				/>

				<SizeUnit
					units={units}
					activeUnit={activeUnit}
					onClick={(unit) => handleChange("updateUnit", unit)}
				/>
			</div>

			<div className={`${prefix}-controller`}>
				<div className={`${prefix}-lists`}>
					<InputGroup
						property={properties}
						isLinkActive={isLinkActive}
						updateLinkState={() => handleChange("updateLink")}
						type="number"
						lists={values[activeView]}
						onChange={(i, v) =>
							handleChange("updateValues", { index: i, value: v })
						}
					/>
				</div>
			</div>
			<div className={`${prefix}-btn-reset-wrapper`}>
				<Button onClick={reset} className={`is-button is-default is-small`}>
					{__("Reset", "rise-blocks")}
				</Button>
			</div>
		</div>
	);
};

export default DimensionControl;

globalize("DimensionControl", DimensionControl);
