import { registerBlockType } from "@wordpress/blocks";
import { createHooks } from "@wordpress/hooks";
import { SVG, Path, G } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import { select } from "@wordpress/data";

import { prefix } from "../constants";
import _has from "lodash/has";
import _isUndefined from "lodash/isUndefined";
import _pickBy from "lodash/pickBy";
import _union from "lodash/union";
import _findIndex from "lodash/findIndex";
import _merge from "lodash/merge";

export const isUndefined = _isUndefined;
export const pickBy = _pickBy;
export const has = _has;
export const union = _union;
export const findIndex = _findIndex;
export const merge = _merge;

import clsnames from "classnames";
export const classnames = clsnames;
export const insertableBlocks = [];
export const register = (name, payload, prefix = "rise-blocks") => {
	if (
		!payload.supports ||
		(payload.supports && payload.supports.inserter === true)
	) {
		insertableBlocks.push(name);
	}
	registerBlockType(name, getPayload(payload));
};

export const globalize = (name, component) => {
	if (isUndefined(wp.riseBlocks)) {
		wp.riseBlocks = {};
	}

	wp.riseBlocks[name] = component;
};

export const wordTrim = (sentence, amount, tail = "...") => {
	const words = isUndefined(sentence) ? "" : sentence.split(" ");

	if (amount >= words.length) {
		return sentence;
	}

	const truncated = words.slice(0, amount);
	return `${truncated.join(" ")}${tail}`;
};

export const capitalize = (s) => {
	if (typeof s !== "string") return "";
	return s.charAt(0).toUpperCase() + s.slice(1);
};

export const getPayload = ({ icon, category, ...rest }) => ({
	...rest,
	icon: icon ? icon : "universal-access-alt",
	category: category ? category : "rise-blocks",
});

export const getArray = (count = 1, value = 0, isArray = false) => {
	if (isArray) {
		return value;
	} else {
		return new Array(parseInt(count > 0 ? count : 1)).fill(value);
	}
};

export const getClass = (cls) => `${prefix}-${cls}`;

export const getTypoStyle = (typo) => {
	if (!typo) return {};
	const { fontSize, fontFamily, fontWeight, lineHeight, textTransform } = typo;

	return {
		fontFamily,
		fontWeight,
		textTransform,
		lineHeight: lineHeight.values.desktop + lineHeight.activeUnit,
		fontSize: fontSize.values.desktop + fontSize.activeUnit,
	};
};

export const getDimensionStyle = (props, setting, device = "desktop") => {
	if (props === "margin") {
		props = ["marginTop", "marginRight", "marginBottom", "marginLeft"];
	} else if (props === "padding") {
		props = ["paddingTop", "paddingRight", "paddingBottom", "paddingLeft"];
	} else if (props === "border-redius") {
		props = [
			"border-top-left-radius",
			"border-top-right-radius",
			"border-bottom-left-radius",
			"border-bottom-right-radius",
		];
	}

	if (!setting) return {};
	let style = {};

	const { values, activeUnit } = setting;
	props.map((v, k) => {
		style[v] = values[device][k]
			? values[device][k] + activeUnit
			: values[device][k];
	});

	return style;
};

export const getResponsiveRangeVal = (data, device = "desktop") => {
	if (!Object.keys(data).length) return {};

	const { activeUnit, values } = data;

	return values[device] + activeUnit;
};

/* For handle gutter of child blocks */
export const getInnerBlocks = (clientsId) =>
	select("core/block-editor").getBlocks(clientsId);

export const setColWidth = (
	id,
	itemsPerRow,
	gutter,
	type = "parent",
	initGap = 0,
) => {
	const width = `calc( ${100 / itemsPerRow}% - ${
		(gutter * (itemsPerRow - 1)) / itemsPerRow
	}px)`;
	const margin = `${gutter / 2 + initGap}px 0`;

	const styleEle = ($ele) => {
		$ele.style.width = width;
		$ele.style.margin = margin;
	};

	if (type === "child") {
		const $ele = document.getElementById(`block-${id}`);
		$ele && styleEle($ele);
		return;
	}

	const innerBlocksId = getInnerBlocks(id).map(
		(innerBlock) => innerBlock.clientId,
	);
	innerBlocksId.forEach((id) => {
		const $ele = document.getElementById(`block-${id}`);
		$ele && styleEle($ele);
	});
};

export const addWrapperClass = (
	clientId,
	className = "icon-blocks-wrapper",
) => {
	/* Add custom class into subblocks parent div for flex layout */
	const $ele = document.getElementById(`block-${clientId}`);
	const prefixClassName = `${prefix}-${className}`;
	
	if (!$ele) return;

	if (!$ele.parentElement.classList.contains(prefixClassName)) {
		$ele.parentElement.classList.add(prefixClassName);
	}
};

export const getParentAttrs = (props) => {
	const { clientId } = props;
	const parentId = select("core/block-editor").getBlockRootClientId(clientId);
	if (parentId) {
		const parentBlockDetail = select("core/block-editor").getBlock(parentId);
		const { attributes } = parentBlockDetail;
		return attributes;
	}

	return false;
};

export const setInitialAttrs = (props, editorNSaveAttrs) => {
	const { setAttributes } = props;

	const defaultAttr = {
		...editorNSaveAttrs,
		isInit: true,
	};
	setAttributes(defaultAttr);
};

export const getInnerBlocksCount = (clientId) => {
	const blocks = select("core/block-editor").getBlocksByClientId(clientId)[0];
	return blocks ? blocks.innerBlocks.length : 0;
};

export const innerBlocksLimit = wp.hooks.applyFilters("riseBlocksInnerBlockLimit", 4);

export const shouldShowAddButton = (clientId) =>
	clientId && getInnerBlocksCount(clientId) < innerBlocksLimit;
export const shouldShowUpgrader = (clientId) => {
	const parentId = select("core/block-editor").getBlockRootClientId(clientId);
	if (!parentId) {
		return false;
	}
	const blocks =
		select("core/block-editor").getBlocksByClientId(parentId)[0].innerBlocks;
	const i = findIndex(blocks, { clientId });
	return i >= innerBlocksLimit;
};

export const downloadAsJsonFile = (fileName, data) => {
	const json = JSON.stringify(data);
	const blob = new Blob([json], { type: "application/json" });
	const href = URL.createObjectURL(blob);
	const link = document.createElement("a");
	link.href = href;
	link.download = fileName + ".json";
	document.body.appendChild(link);
	link.click();
	document.body.removeChild(link);
};

export const getLocalImage = (name) => {
	return Rise_Blocks_VAR.plugin_path + "img/" + name;
};

export const isPro = () => Rise_Blocks_VAR.is_pro == "1";

export const exportToJsonFile = (clientId) => {
	const iterator = ({ name, attributes, innerBlocks }) => {
		if (innerBlocks.length > 0) {
			return {
				name,
				attributes,
				inner: innerBlocks.map((child) => iterator(child)),
			};
		}

		return { name, attributes };
	};

	const blocks = select("core/block-editor").getBlocksByClientId(clientId);
	const data = blocks.map((a) => iterator(a));
	downloadAsJsonFile(blocks[0].name.replace("/", "-") + "-" + clientId, data);
};

