import { useState } from "@wordpress/element";
import { Button, SelectControl } from "@wordpress/components";
import { __ } from "@wordpress/i18n";
import { merge } from "../helpers";
import ResponsiveRangeControl from "../components/responsive-range-control";

import {
	prefix,
	defaultResponsiveViews,
	defaultFontFamily,
	defaultFontWeight,
	defaultTextTransform,
} from "../constants";

const Typography = (props) => {
	const [showAdvanceOptions, setShowAdvanceOptions] = useState(false);
	const [activeView, setActiveView] = useState("desktop");

	const toggleAdvanceOptions = () => {
		setShowAdvanceOptions(!showAdvanceOptions);
	};

	const reset = () => {
		const { defaultValue, onChange } = props;
		defaultValue && onChange(defaultValue);
	};

	const handleChange = (k, v) => {
		const value = getValue();
		const data = merge({}, value, { [k]: v });
		props.onChange(data);
	};

	const getValue = () => {
		const { value } = props;
		return value ? value : {};
	};

	const changeActiveView = (activeView) => {
		setActiveView(activeView);
	};

	let typography = getValue();
	const { label = __("Typography", "rise-blocks") } = props;
	const { lineHeight, fontFamily, fontWeight, fontSize, textTransform } =
		typography;

	return (
		<div className={`${prefix}-typegraphy-options`}>
			<div className={`${prefix}-typography-options-actions`}>
				<label className="components-base-control__label"> {label} </label>
				<div>
					<Button
						onClick={toggleAdvanceOptions}
						className="is-button is-default is-small"
					>
						<span className="dashicons dashicons-admin-tools"></span>
					</Button>
					<Button
						onClick={reset}
						className={`is-button is-default is-small ${prefix}-btn-reset`}
					>
						{__("Reset", "rise-blocks")}
					</Button>
				</div>
			</div>

			{showAdvanceOptions && (
				<div className={`${prefix}-typography-advanced`}>
					<ResponsiveRangeControl
						changeActiveView={changeActiveView}
						activeView={activeView}
						step="0.1"
						name="fontSize"
						responsiveViews={defaultResponsiveViews}
						value={fontSize}
						onChange={handleChange}
						label={__("Font Size", "rise-blocks")}
					/>

					<SelectControl
						label={__("Font Family", "rise-blocks")}
						value={fontFamily}
						onChange={(f) => {
							handleChange("fontFamily", f);
						}}
						options={defaultFontFamily}
					/>

					<SelectControl
						label={__("Text transform", "rise-blocks")}
						value={textTransform}
						onChange={(t) => handleChange("textTransform", t)}
						options={defaultTextTransform}
					/>

					<SelectControl
						label={__("Font Weight", "rise-blocks")}
						value={fontWeight}
						onChange={(fw) => {
							handleChange("fontWeight", fw);
						}}
						options={defaultFontWeight}
					/>

					<ResponsiveRangeControl
						changeActiveView={changeActiveView}
						activeView={activeView}
						step="0.1"
						name="lineHeight"
						value={lineHeight}
						onChange={handleChange}
						responsiveViews={defaultResponsiveViews}
						label={__("Line Height", "rise-blocks")}
					/>
				</div>
			)}
		</div>
	);
};
export default Typography;
