import { __ } from '@wordpress/i18n';
import {
	InspectorControls,
	InspectorAdvancedControls,
} from "@wordpress/block-editor";
import { PanelBody, ColorIndicator, QueryControls , RangeControl } from "@wordpress/components";
import { addQueryArgs } from "@wordpress/url";
import apiFetch from '@wordpress/api-fetch';
import { createHooks } from "@wordpress/hooks";

import { useEffect, useState } from "@wordpress/element";

import {
  SwitchControl,
  PanelColor,
  ColorControl,
} from '../../../custom-control';

import { prefix } from '../../../constants';

import Typography from '../../../custom-control/typography';
import { ImageSizeControl } from '../../../custom-control';
import Tab from '../../../components/tab';
import Dimension from '../../../custom-control/dimension';

import metadata from "../block.json";
import Exporter from '../../../components/exporter';

const InspectorPanel = (props) => {
    const [categoriesList, setCategoriesList] = useState([]);
    const { applyFilters } = createHooks();

    useEffect(() => {
        const onGet = (categoriesList) => {
            setCategoriesList(categoriesList);
        };

        const onError = () => {
            setCategoriesList([]);
        };

        const CATEGORIES_LIST_QUERY = {
            per_page: -1,
        };

        const params = {
            path: addQueryArgs(`/wp/v2/categories`, CATEGORIES_LIST_QUERY),
        };

        if (categoriesList.length === 0) {
            apiFetch(params).then(onGet).catch(onError);
        }
    }, [categoriesList]);
  
    const {
      attributes: {
        order,
        orderBy,
        categories,
        postsToShow,
        headingTypo,
        titleTypo,
        metaTypo,
        enableTitle,
        enableCategory,
        enableAuthor,
        enableDate,
        enableImage,
        imageSize,
        color,
        bgColor,
        padding,
        arrowBgColor,
        arrowColor,
        slidesToShow,
      },
      setAttributes,
      clientId,
    } = props;
  
    const bgData = {
      arrowBgColor: {
        value: arrowBgColor,
        title: __('Bg', 'rise-blocks'),
      },
      arrowColor: {
        value: arrowColor,
        title: __('Arrow', 'rise-blocks'),
      },
    };
  
    const ButtonBgSetting = Object.keys(bgData).map((key) => {
      const value = bgData[key].value;
      return {
        title: () => (
          <div>
            {bgData[key].title}
            <ColorIndicator colorValue={value} />
          </div>
        ),
        content: () => (
          <ColorControl
            value={value}
            hideIndicator={true}
            onChange={(c) => setAttributes({ [key]: c ? c : 'transparent' })}
          />
        ),
      };
    });
  
    return (
        <>
            <InspectorAdvancedControls>
                <Exporter clientId={clientId} />
            </InspectorAdvancedControls>
            <InspectorControls>
                <PanelBody title={__('General', 'rise-blocks')} initialOpen={true}>
                    <QueryControls
                        order={order}
                        orderBy={orderBy}
                        categoriesList={categoriesList}
                        selectedCategoryId={categories}
                        numberOfItems={postsToShow}
                        onCategoryChange={(value) => setAttributes({ categories: value !== '' ? value : undefined })}
                        maxItems={applyFilters('riseBlocksNews2Limit', 100)}
                        onNumberOfItemsChange={(value) => setAttributes({ postsToShow: value })}
                        onOrderChange={(value) => setAttributes({ order: value })}
                        onOrderByChange={(value) => setAttributes({ orderBy: value })}
                    />
                </PanelBody>
    
            <PanelBody title={__('Slider', 'rise-blocks')} initialOpen={false}>
                <RangeControl
                label={__('Slides To Show', 'rise-blocks')}
                allowReset={true}
                min={1}
                max={4}
                value={slidesToShow}
                initialPosition={3}
                onChange={(slidesToShow) => setAttributes({ slidesToShow })}
                />
            </PanelBody>
  
            <PanelBody title={__( 'Content', 'rise-blocks' )} initialOpen={false}>
                <SwitchControl
                    label={__('Enable Title', 'rise-blocks')}
                    checked={enableTitle}
                    onChange={(enableTitle) => { setAttributes({ enableTitle }) }}
                />

                <SwitchControl
                    label={__('Enable Category', 'rise-blocks')}
                    checked={enableCategory}
                    onChange={(enableCategory) => { setAttributes({ enableCategory }) }}
                />
                <SwitchControl
                    label={__('Enable Author', 'rise-blocks')}
                    checked={enableAuthor}
                    onChange={(enableAuthor) => { setAttributes({ enableAuthor }) }}
                /> 
                <SwitchControl
                    label={__('Enable Date', 'rise-blocks')}
                    checked={enableDate}
                    onChange={(enableDate) => { setAttributes({ enableDate }) }}
                />
            </PanelBody>

            <PanelBody title={__( 'Image', 'rise-blocks' )} initialOpen={false}>
                <ImageSizeControl
                    value={imageSize}
                    onChange={imageSize => { setAttributes({ imageSize }) }}
                />
            </PanelBody>

        <PanelBody title={__( 'Typography', 'rise-blocks' )} initialOpen={false}>
            <Typography
                label={__('Heading', 'rise-block')}
                value={headingTypo}
                defaultValue={metadata.attributes.headingTypo.default}
                onChange={headingTypo => setAttributes({ headingTypo })} 
            />
            <hr />
            <Typography
                label={__('Title', 'rise-block')}
                value={titleTypo}
                defaultValue={metadata.attributes.titleTypo.default}
                onChange={titleTypo => setAttributes({ titleTypo })} 
            />
            <hr />
            
            <Typography
                label={__('Meta', 'rise-block')}
                value={metaTypo}
                defaultValue={metadata.attributes.metaTypo.default}
                onChange={metaTypo => setAttributes({ metaTypo })} 
            />
        </PanelBody>

        <PanelBody title={__('Spacing', 'rise-blocks')} initialOpen={false}>
            <Dimension
                label={__('Padding', 'rise-block')}
                value={padding}
                defaultValue={metadata.attributes.padding.default}
                onChange={padding => setAttributes({ padding })} />
        </PanelBody>       
  
            <PanelColor
                colorSettings={[
                {
                    value: color,
                    onChange: (color) => setAttributes({ color }),
                    label: __('Text Color', 'rise-blocks'),
                },
                {
                    value: bgColor,
                    onChange: (bgColor) => setAttributes({ bgColor }),
                    label: __('Background Color', 'rise-blocks'),
                },
                ]}
            >
                <Tab className={`${prefix}-tabbed-color-control`} label={__('Arrow Color', 'rise-blocks')} data={ButtonBgSetting} />
            </PanelColor>
        </InspectorControls>
      </>
    );
};
  
export default InspectorPanel;
