import { SelectControl, RangeControl, ColorIndicator, BaseControl, ColorPalette, TextControl, ToggleControl, ColorPicker } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { PanelColorSettings } from '@wordpress/block-editor';
import { colors, backgroundPosition } from '../constants';

export const ItemPerRowControl = ( { label, onChange, ...rest } ) => (
	<TextControl
	    label={ label ? label : __( 'Number of Items per Row', 'rise-blocks' ) }
	    type="number"
	    min={1}
	    max={4}
	    onChange={ perRow => onChange( parseInt( perRow ) ) }
	    { ...rest }
	/>
);

export const HeadingTagControl = ( { label, ...rest } ) => (
	<SelectControl
		label={ label ? label: __( 'Tag', 'rise-blocks' ) }
		options={[
			{ label: 'H1', value: 'h1' },
			{ label: 'H2', value: 'h2' },
			{ label: 'H3', value: 'h3' },
			{ label: 'H4', value: 'h4' },
			{ label: 'H5', value: 'h5' },
			{ label: 'H6', value: 'h6' },
			{ label: 'Span', value: 'span' },
			{ label: 'P', value: 'p' },
		]}
		{ ...rest }
	/>
);

export const HtmlTagControl = ( { label, ...rest } ) => (
	<SelectControl
		label={ label ? label: __( 'Tag', 'rise-blocks' ) }
		options={[
			{ label: 'H1', value: 'h1' },
			{ label: 'div', value: 'div' },
			{ label: 'p', value: 'p' },
		]}
		{ ...rest }
	/>
);

export const BackgroundPosition = ({
    label,
    onChange,
    ...rest
}) =>(
    <SelectControl
        label={ label || __('Background Position', 'rise-blocks')}
        options={ backgroundPosition }
        onChange={ backgroundPosition => onChange(backgroundPosition) } 
        { ...rest }
    />
);

export const FontSizeControl = ( { label, allowReset=true, ...rest } ) => (
	<RangeControl
        label={ label === false 
					? false 
					: label 
						? label 
						: __( 'Font Size', 'rise-blocks' ) 
			}
        allowReset={ allowReset }
        min={ 10 }
        max={ 100 }
        beforeIcon="editor-textcolor"
        {...rest}
    />
);

export const ThicknessControl = props => (
	<RangeControl
        label={ __( 'Thickness', 'rise-blocks' ) }
        allowReset={ true }
        min={ 1 }
        max={ 50 }
        { ...props }
    />
);

export const BorderWidthControl = props => (
	<RangeControl
        label={ __( 'Border Width', 'rise-blocks' ) }
        allowReset={ true }
        min={ 0 }
        max={ 50 }
        { ...props }
    />
);

export const WidthControl = ( props ) => (
	<RangeControl
        label={ __( 'Width', 'rise-blocks' ) }
        allowReset={ true }
        min={ 1 }
        max={ 120 }
        { ...props }
    />
);

export const OpacityControl = ( { label, ...rest } ) => (
	<RangeControl
        label={ label ? label : __( 'Opacity', 'rise-blocks' ) }
        allowReset={ true }
        step={0.1}
    	min={ 0 }
		max={ 1 }
        { ...rest }
    />
);

export const SpacingControl = ( { label, beforeIcon, ...rest } ) => (
	<RangeControl
        label={ label ? label : __(  'Spacing', 'rise-blocks' ) }
        beforeIcon={ beforeIcon? beforeIcon : "arrow-down-alt" }
        allowReset={ true }
        min={ 0 }
        max={ 100 }
        { ...rest }
    />
);


export const ColorControl = ( { label, hideIndicator, ...rest } ) => {

	const color = <ColorPalette 
    	className="editor-color-palette-control__color-palette"
    	colors={ colors }
		{ ...rest } 
	/>;
	let indicator = false;
	if( hideIndicator ){
		return color;
	}else{

		indicator = <>
			{ ( label ? label : __( 'Color', 'rise-blocks' ) ) } 
			<ColorIndicator colorValue={ rest.value } />
		</>;

		return <BaseControl label={ indicator }>
		   { color }
		</BaseControl>
	}
	
};

export const BackgroundOverlayControl = ( { label, onChange, value, ...rest } ) => {

	const indicator = <>
		{ ( label ? label : __( 'Background Overlay', 'rise-blocks' ) ) } 
		<ColorIndicator colorValue={ value } />
	</>;

	return(
		<BaseControl label={ indicator }>
		    <ColorPicker
		    	color={ value }
		    	onChangeComplete={ ( {rgb} ) => onChange( `rgba(${rgb.r}, ${rgb.g}, ${rgb.b}, ${rgb.a} )` ) }
	           { ...rest }
		    />
		</BaseControl>
	);
}

export const BgControl = ( { label, ...rest } ) => (
	<SelectControl
	    label={ label ? label : __( 'Background Type', 'rise-blocks' ) }
	    options={ [
	        { label: __( 'Image', 'rise-blocks' ), value: 'image' },
	        { label: __( 'Color', 'rise-blocks' ), value: 'color' },
	        { label: __( 'Video', 'rise-blocks' ), value: 'video' }
	    ] }
	    { ...rest }
	/>
);

export const ImageSizeControl = ( { label, ...rest } ) => (
	<SelectControl
	    label={ label ? label : __( 'Image Size', 'rise-blocks' ) }
	    options={ Rise_Blocks_VAR.image_size.filter( v => 'medium_large' != v ).map( (v) => ( { label: v, value: v } ) ) }
	    { ...rest }
	/>
);

export const LinkControl = ({ label, ...rest }) => (
	<TextControl
	    label={ label ? label : __( 'URL', 'rise-blocks' ) }
	    type="url"
	    { ...rest }
	/>
);

export const BorderStyleControl = ({ label, ...rest }) => (
	<SelectControl
	    label={ label ? label : __( 'Border Style', 'rise-blocks' ) }
	    options={ [
	        { label: __( 'None', 'rise-blocks' ), value: 'none' },
	        { label: __( 'Solid', 'rise-blocks' ), value: 'solid' },
	        { label: __( 'Dashed', 'rise-blocks' ), value: 'dashed' },
	        { label: __( 'Double', 'rise-blocks' ), value: 'double' },
	        { label: __( 'Dotted', 'rise-blocks' ), value: 'dotted' },
	    ] }
	    { ...rest }
	/>
);

export const RadiusControl = ( { label, ...rest } ) => (
	<RangeControl
        label={ label ? label : __( 'Icon Radius', 'rise-blocks' ) }
        allowReset={ true }
        min={ 0 }
        max={ 50 }
       { ...rest }
    />
);

export const SwitchControl = params => <ToggleControl {...params} />;

export const ExcerptLengthControl = ( { label, ...rest } ) => (
	<RangeControl
        label={ label ? label : __( 'Excerpt Length', 'rise-blocks' ) }
        allowReset={ true }
        min={ 1 }
        max={ 60 }
        initialPosition={ 20 }
        { ...rest }
    />
);

export const PanelColor = ( { children, ...rest } ) => (
	<PanelColorSettings
		title={ __( 'Color', 'rise-blocks' ) }
		initialOpen={ false }
		colors={ colors }
		{ ...rest }
	>
		{ children }
		
	</PanelColorSettings>
);

export const FixedBackgroundColor = ( { label, ...rest } ) => (
	<SwitchControl
		label={ label ? label : __( 'Fixed Background', 'rise-blocks' ) }
		{ ...rest }
	/>
);	 