import ResponsiveViewsIcons from "./responsive-view-icons";
import SizeUnit from "./size-unit";
import { defaultResponsiveRange, prefix, sizeUnit } from "../constants";
import { merge } from "../helpers";
import { FontSizeControl } from "../custom-control";
import { useState } from "@wordpress/element";

const ResponsiveRangeControl = (props) => {
    const [activeUnit, setActiveUnit] = useState(null);

    const handleChange = (property, value) => {
        const oldData = getAttributesValues();
        const { onChange, name, activeView } = props;

        const obj = {
            [property]: property === "activeUnit" ? value : { [activeView]: value },
        };
        const newData = merge({}, oldData, obj);
        onChange(name, newData);
    };

    const getAttributesValues = () => {
        const { value } = props;
        return value
            ? merge({}, defaultResponsiveRange, value)
            : defaultResponsiveRange;
    };

    const {
        label,
        responsiveViews,
        activeView,
        changeActiveView,
        step = "1",
        beforeIcon = "editor-textcolor",
    } = props;
    const {
        units = sizeUnit,
        values,
        range: { min, max },
    } = getAttributesValues();

    return (
        <div className={`${prefix}-responsive-range-control`}>
            <div className={`${prefix}-label-section`}>
                <label className="components-base-control__label">{label}</label>
                <ResponsiveViewsIcons
                    responsiveViews={responsiveViews}
                    activeView={activeView}
                    onClick={(view) => changeActiveView(view)}
                />

                <SizeUnit
                    units={units}
                    activeUnit={activeUnit}
                    onClick={(v) => {
                        setActiveUnit(v);
                        handleChange("activeUnit", v);
                    }}
                />
            </div>

            <FontSizeControl
                step={step}
                min={min}
                max={max}
                beforeIcon={beforeIcon}
                label={false}
                allowReset={false}
                value={values[activeView]}
                onChange={(v) => handleChange("values", v)}
            />
        </div>
    );
};

export default ResponsiveRangeControl;