import { TextControl, Button } from '@wordpress/components';
import { prefix } from '../constants';

const InputGroup = ({
  lists,
  onChange,
  type,
  className = '',
  updateLinkState,
  isLinkActive,
  property
}) => (
  <ul className={className}>
    {lists.map((value, index) => (
      <li key={index}>
        <TextControl
          type={type}
          value={value}
          onChange={value => {
            onChange(index, value);
          }}
        />
        {property && property[index] && <label>{property[index]}</label>}
      </li>
    ))}
    <li>
      <Button
        className={`${prefix}-link ${isLinkActive ? 'linked' : ''}`}
        onClick={updateLinkState}
      >
        <span
          className={`dashicons ${
            isLinkActive ? 'dashicons-admin-links' : 'dashicons-editor-unlink'
          }`}
        />
      </Button>
    </li>
  </ul>
);

export default InputGroup;