import { BaseControl  } from "@wordpress/components";
import { Fragment, useState } from "@wordpress/element";
import { __ } from "@wordpress/i18n";

import { prefix } from "../constants";

function Tab(props) {
	const [active, setActive] = useState(0);

	const onClick = (i) => (e) => {
		e.preventDefault();
		setActive(i);
	};

	const { data, className, label } = props;

	const titles = data.map((v, i) => {
		let title = typeof v.title === "function" ? v.title() : v.title;

		return (
			<li key={i}>
				<a
					href="#"
					onClick={onClick(i)}
					className={` ${active === i ? "active" : ""} tab-anchor`}
				>
					{title}
				</a>
			</li>
		);
	});

	const cls = `${prefix}-tab-wrapper ${className}`;

	const content = (
		<Fragment>
			<ul className={`${prefix}-custom-tab`}>{titles}</ul>
			<div className={`${prefix}-tab-content`}>{data[active].content()}</div>
		</Fragment>
	);

	if (label) {
		return (
			<BaseControl className={cls} label={label}>
				{content}
			</BaseControl>
		);
	}

	return <div className={cls}>{content}</div>;
}

export default Tab;
